<template>
  <div class="turn-page" v-if="totalPage>1">
    <div class="prev-page" :class="prevClass" @click="turnPage('prev')">&lt;</div>
    <template v-if='totalPage<=7'>
      <div
        v-for="i in totalPage"
        :key="i"
        :class="{ active: i === nowPage }"
        @click="turnPage(i)">
          {{ i }}
        </div>
    </template>
    <template v-else>
      <template v-if="nowPage<=4">
        <div
          v-for='i in 6'
          :key='i'
          :class="{ active: i === nowPage }"
          @click="turnPage(i)"
        >
          {{i}}
        </div>
        <div @click="turnPage(nowPage+5>totalPage?totalPage:nowPage+5)">...</div>
        <div
          :class="{ active: totalPage === nowPage }"
          @click="turnPage(totalPage)"
        >{{totalPage}}</div>
      </template>
      <template v-if="nowPage>4&&nowPage<=totalPage-4">
        <div
          :class="{ active: 1 === nowPage }"
          @click="turnPage(1)"
        >{{1}}
        </div>
        <div @click="turnPage(nowPage-5>1?nowPage-5:1)">...</div>
        <div
          v-for='i in 2'
          :key='i.toString()'
          @click="turnPage(nowPage-3+i)"
        >
          {{nowPage-3+i}}
        </div>
        <div class="active">{{nowPage}}</div>
        <div
          v-for='i in 2'
          :key='nowPage+i'
          @click="turnPage(nowPage+i)"
        >
          {{nowPage+i}}
        </div>
        <div @click="turnPage(nowPage+5>totalPage?totalPage:nowPage+5)">...</div>
        <div
          :class="{ active: totalPage === nowPage }"
          @click="turnPage(totalPage)"
        >{{totalPage}}</div>
      </template>
      <template v-if="nowPage>totalPage-4">
        <div
          :class="{ active: 1 === nowPage }"
          @click="turnPage(1)"
        >{{1}}</div>
        <div @click="turnPage(nowPage-5>1?nowPage-5:1)">...</div>
        <div
          v-for='i in 6'
          :key='i'
          :class="{ active: i+totalPage-6 === nowPage }"
          @click="turnPage(i+totalPage-6)"
        >
          {{i+totalPage-6}}
        </div>
      </template>
    </template>

    <div class="next-page" :class="nextClass" @click="turnPage('next')">&gt;</div>
  </div>
</template>

<script>
export default {
  props: {
    totalPage: {
      type: Number,
      default: 100,
    },
    nowPage: {
      type: Number,
      default: 1,
    },
  },
  methods: {
    turnPage(page) {
      let np = null;

      if (page === 'prev') {
        if (this.nowPage > 1) {
          np = this.nowPage - 1;
        }
      } else if (page === 'next') {
        if (this.nowPage < this.totalPage) {
          np = this.nowPage + 1;
        }
      } else {
        console.log(this.nowPage);
        np = page;
      }
      this.$emit('current-page', np);
    },
  },
  computed: {
    prevClass() {
      if (this.nowPage === 1) {
        return 'no-allow';
      }
      return '';
    },
    nextClass() {
      if (this.nowPage === this.totalPage) {
        return 'no-allow';
      }
      return '';
    },
  },
};
</script>

<style scoped>
  .turn-page {
    display: flex;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    user-select: none;
  }
  .turn-page > div {
    padding: 0 4px;
    font-size: 13px;
    min-width: 35.5px;
    height: 28px;
    line-height: 28px;
    cursor: pointer;
    box-sizing: border-box;
    text-align: center;
    margin: 0;
    font-weight: 600;
  }
  .turn-page > div:hover {
    color: #409eff;
  }
  .turn-page > .active {
    color: #409eff;
    cursor: default;
  }
  .no-allow{
    cursor: not-allowed!important;
  }
  .no-allow:hover{
    color: #000!important;
  }
</style>>
