import Vue from 'vue';
import toast from './index.vue';

// 顶部弹框组件

const Toast = Vue.extend(toast);
// new Toast相当于new Vue(options)
const showToast = ({ msg, type }) => {
  const app = new Toast({
    el: document.createElement('div'),
    data() {
      return {
        msg,
        type,
      };
    },
  });
  document.body.appendChild(app.$el);
  setTimeout(() => {
    app.show = false;
  }, 1500);
  setTimeout(() => {
    app.exist = false;
  }, 2500);
};
export default showToast;
